<template>
  <div class="reset-psw-view view-background">
    <header-view :fixed="fixed" :title="title" :routerName="routerName"></header-view>
    <div class="refund-cue">温馨提示：<br/>1.限时特价、预约资格等购买优惠可能一并取消<br/>2.如遇订单拆分，京券将换成同价值京豆返还<br/>3.白条券无法返还<br/>4.订单一旦取消，无法恢复</div>
    <div class="setting-list-item bw" @click.prevent.stop="visible"><span class="setting-label">退款原因：</span>{{reason}}<i class="icon iconfont icon-more fr"></i></div>
    <div class="setting-list-item bw"><span class="setting-label">退款联系人：</span>{{name}}</div>
    <div class="setting-list-item bw"><span class="setting-label">联系方式：</span>{{tel}}</div>
    <div class="login-in" :class="{'sunmit':isSubmit}" @click="confirm">申请退款</div>
    <mt-popup v-model="popupVisible" position="bottom" style="width:100%">
      <div class="flex-box">
        <div class="type-oper" @click="cacelModal">取消</div>
        <div class="flex-center"></div>
        <div class="type-oper success" @click="selectType">确定</div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '申请退款',
        fixed: false,
        routerName: 'custom',
        popupVisible: false,
        selectedReason: '',
        reason: '请先选择退款原因',
        name: 'sad',
        tel: 123165,
        slots: [{
          flex: 1,
          values: ['让你知道做生意不容易', '没什么', '好评点了怎么没反应，试试退款可以不', '就是想退'],
          defaultIndex: 0,
          className: 'slots',
          textAlign: 'center'
        }]
      }
    },
    created: function () {
      if (this.$route.params.name) {
        this.routerName = this.$route.params.name // 上个路由带过来
        localStorage.prev = this.$route.params.name
      } else {
        this.routerName = localStorage.prev
      }
    },
    methods: {
      onValuesChange: function (picker, values) {
        if (values[0] === undefined) {
          picker.values[0] = this.slots[0].values[0]
          this.selectedReason = this.slots[0].values[0]
        } else {
          this.selectedReason = values.toString()
        }
      },
      visible: function () {
        this.popupVisible = true
      },
      selectType: function () {
        this.reason = this.selectedReason
        this.popupVisible = false
      },
      cacelModal: function () {
        this.popupVisible = false
      },
      confirm: function () {
        if (this.isSubmit) {
          this.$router.push({
            name: 'orderDetail'
          })
        }
      }
    },
    computed: {
      isSubmit: function () {
        if (this.reason !== '请先选择退款原因') {
          return true
        }
      }
    }
  }
</script>

<style>
.refund-cue{
    margin: .1rem .12rem;
    padding: .24rem .12rem;
    background-color: white;
}.reset-psw-view {
  height: 100%;
}.view-background {
  background-color: #F5F5F5;
}.bw{
  background-color: white;
  line-height: .5rem;
}
.setting-list-item {
  clear: both;
  height: .55rem;
  color: #333;
  padding: 0 .15rem;
  line-height: .55rem;
  border-bottom: .01rem solid #E5E5E5;
}
.login-in {
    position: absolute;
    width: 80%;
    height: 7%;
    line-height: .45rem;
    left: 50%;
    margin-top: .3rem;
    text-align: center;
    color: lightsteelblue;
    transform: translateX(-50%);
    border-radius: .3rem;
    background-color: #E5E5E5;
}
.sunmit{
    background:url(/static/image/login/login_bar_dis@3x.png) no-repeat ;
    background-size: 100% 100%;
    color: white;
}
.fr {
    float: right;
}
.flex-box{
  display: flex;
    background-color: #ff9b24;
}
.type-oper{
    flex: 1;
    text-align: center;
    color: white;
    padding: .1rem 0;
}
.flex-center{
  flex: 3;
}
</style>


