<template>
  <mt-header :fixed="isfixed" :title="pageTitle">
    <mt-button slot="left"  icon="back" @click="ceshi"></mt-button>
    <mt-button slot="right" @click="rightTextClick">{{rightLabel}}</mt-button>
  </mt-header>
</template>

<script>
  export default {
    props: ['title', 'routerName', 'fixed', 'rightText'],
    data () {
      return {
        name: (this.routerName === undefined ? '' : this.routerName),
        isfixed: this.fixed === undefined ? true : this.fixed,
      }
    },
    computed: {
      pageTitle: function () {
        return this.title
      },
      rightLabel: function () {
        return this.rightText === undefined ? '' : this.rightText
      }
    },
    methods: {
      rightTextClick: function () {
        this.$emit('rightClick')
      },
      ceshi:function () {
        this.$router.goback()
      }
    }
  }
</script>

<style>

</style>


